import validator from '@/utils/validator.js'
import T from '@/utils/tips.js'
import { postUserLogin, getUserInfoData, postUserSms, weixinLogin } from '@/api/userApi.js'
export default {
	data() {
		return {
			phone:'',
			code:'',
			codeText: '获取验证码',
			codeNum: '', // 定时器时间
			isRight: false, // 是否完成输入
			setCodeInterval: '', // 定时器
			deviceId: '', // 数据传值deviceId
			appID: 'wxb8afa388fa540c2a',
			fromWxLoginData:''
		}
	},
	components: {
	
	},
	onLoad(options) {
		this.fromWxLoginData = JSON.parse(options.data) 
		console.log('fromWxLoginData->',this.fromWxLoginData)
	},
	onShow() {
		uni.setStorageSync('isLogin',0)	
	},
	methods: {
		
		doIsLogin() {
			this.isRight = !validator.isPhone(this.phone) && this.code !== ''
		},
	    // 获取验证码
	    getCode() {
	      console.log(validator.isPhone(this.phone))
	      // 已发送未超过60秒直接返回
	      if (this.codeNum !== '') {
	        return false
	      }
	      // 手机验证
	      if (validator.isPhone(this.phone)) {
	        if (this.phone === '') {
			  T.tips('手机号不能为空');
	          return false
	        }
			T.tips('手机号码不正确');
	        return false
	      }
		  
	      // 获取uuid
	      this.deviceId = this.getUUID()
	      // 获取手机验证码
	      let data = {
	        mobile: this.phone,
	        deviceId: this.deviceId
	      }
	      postUserSms(data).then((res) => {
			T.tips(res.message)
	        if (res.code === '1000') {
	          this.codeText = '重新发送'
	          this.codeNum = 59
	          this.setCodeInterval = setInterval(() => {
	            if (this.codeNum === 0) {
	              this.codeNum = ''
	              clearInterval(this.setCodeInterval)
	            } else {
	              this.codeNum--
	            }
	          }, 1000)
	        }
	      }).catch((err) => {
			T.tips(err.message || '错误')
	      })
	    },
	    // 登录
	    dologin: function () {
	      if (this.isRight) {




			  this.deviceId = this.getUUID()

			  
		
			  
			  let data = {}
			  if(this.fromWxLoginData.grant_type == 'mini_program'){  //  小程序
				  data = {
					  grant_type: 'mini_program',
					  scope: '2',
					  client_id: 'cwap',
					  client_secret: 'xx',
					  deviceId: this.fromWxLoginData.deviceId,
					  systemId: '2',
					  miniCode: this.fromWxLoginData.miniCode,
					  smsCode: this.code,
					  mobile: this.phone,
					  nickName: this.fromWxLoginData.userInfo.nickName,
					  headImg: this.fromWxLoginData.userInfo.avatarUrl
				  }
			  } else { // APP
				  data = {
					  grant_type: 'wx_app',
					  scope: '2',
					  client_id: 'cwap',
					  client_secret: 'xx',
					  deviceId: this.fromWxLoginData.deviceId,
					  systemId: '2',
					  accessToken:this.fromWxLoginData.accessToken,
					  openId:this.fromWxLoginData.openId,
					  smsCode: this.code,
					  mobile: this.phone
				  }
			  }
			  
	        // let dates = this.$qs.stringify(data)
	        postUserLogin(data).then((res) => {
				console.log(JSON.stringify(res))
				uni.setStorageSync('access_token', res.access_token)
				//uni.setStorageSync('access_token', res.refresh_token)
				uni.setStorageSync('uid', res.id)
				uni.setStorageSync('phone', this.phone)
				if(this.setCodeInterval!=''){
					clearInterval(this.setCodeInterval)
				}
	          // 获取用户信息
	          this.getUserInfo()
	        }).catch((err) => {
				console.log(JSON.stringify(err))
				T.tips(err.message || '登录错误')
	        })
	      }
	    },
	    getUserInfo() {
	      getUserInfoData().then((res) => {
	        if (res.code == '1000') {
				console.log(JSON.stringify(res))
	          uni.setStorageSync('nickName', res.data.nickName)
	          uni.setStorageSync('headImgUrl', res.data.headImgUrl)
			  // 返回上一页
			  uni.navigateBack({
				  delta:2
			  })
	        }
	      }).catch((err) => {
			T.tips(err.message || '获取用户信息错误')
	      })
	    },
	    getUUID() {
	        var s = [];
			var hexDigits = "0123456789abcdef";
			for (var i = 0; i < 36; i++) {
			  s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
			}
			s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
			s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
			s[8] = s[13] = s[18] = s[23] = "-";
		   
			var uuid = s.join("");
			return uuid
	    }
	  }
}
